import React from 'react';
import SoftwareSkill from '../../components/softwareSkills/SoftwareSkill';
import { Fade, Flip } from 'react-reveal';
import './FreelanceCard.scss';
import Button from '../../components/button/Button';

export default function FreelanceCard({ cardInfo, isDark }) {
  const GetDescBullets = ({ descBullets }) => {
    return descBullets
      ? descBullets.map((item) => (
          <p className="freelance-description">{item}</p>
        ))
      : null;
  };

  return (
    <div>
      <Fade>
        <div
          className={
            isDark
              ? 'freelance-card freelance-whiteborder'
              : 'freelance-card freelance-blackborder'
          }
        >
          <div className="freelance-card-left">
            <Flip left>
              <img
                crossOrigin={'anonymous'}
                className="freelance-roundedimg"
                src={cardInfo.companylogo}
                alt={cardInfo.company}
              />
            </Flip>
          </div>
          <div className="freelance-card-right">
            <div className="freelance-title-name-duration">
              <h3 className="freelance-text-company">{cardInfo.company}</h3>
              <small
                className={`${
                  isDark ? 'dark-mode' : ''
                } freelance-text-duration`}
              >
                {cardInfo.date}
              </small>
            </div>

            <div>
              <h4 className="freelance-text-desc">{cardInfo.role}</h4>
              <h5
                className={
                  isDark
                    ? 'dark-mode freelance-text-subHeader'
                    : 'freelance-text-subHeader'
                }
              >
                {cardInfo.desc}
              </h5>
              <div className="freelance-text">
                <GetDescBullets descBullets={cardInfo.descBullets} />
              </div>
            </div>

            {cardInfo.hasHostedWebsite && (
              <Button text={'Open Webpage'} newTab href={cardInfo.website} />
            )}

            <SoftwareSkill list={cardInfo.technologies} />
          </div>
        </div>
      </Fade>
    </div>
  );
}
