import React, { useContext } from 'react';
import './StartupProjects.scss';
import { Fade } from 'react-reveal';
import StyleContext from '../../contexts/StyleContext';
import Button from '../../components/button/Button';
import SoftwareSkill from '../../components/softwareSkills/SoftwareSkill';
import dart from '../../assets/images/dart.svg';
import django from '../../assets/images/django.svg';

export default function StartupProject() {
  const projects = [
    {
      image: require('../../assets/images/newzer.png'),
      projectName: 'Newzer : A News Summarization Platform',
      projectDesc:
        'Developed a short news platform that summarizies the news scrapped from trust-worthy websites',
      descBullets: [
        ' Developed a news portal that summarizes the news content published by leading news distributors over 8+ countries.',

        ' Constructed a pipeline that scrapes news at every 6 hours from trusted news websites and articulates the content within 5 sentences.',

        ' Created an engaging application provides images and videos related to the event occurred and enabled sorting news based on the country and sentiment of the news.',

        ' Integrated a webpage using the data obtained from Yahoo Finance APIs that helps analysing the stock market of different countries.',
      ],
      technologiesUsed: [
        {
          skillName: 'HTML 5',
          imageLink: require('../../assets/images/html5.png'),
        },
        {
          skillName: 'CSS 3',
          imageLink: require('../../assets/images/css3.png'),
        },
        {
          skillName: 'Python',
          imageLink: require('../../assets/images/python.png'),
        },
        {
          skillName: 'Tensorflow',
          imageLink: require('../../assets/images/tensorflow.png'),
        },
        {
          skillName: 'Django',
          imageLink: django,
        },
      ],
      footerLink: [
        {
          name: 'GitHub Link',
          url: 'https://github.com/darshilmehta/newzer',
        },
      ],
    },
    {
      image: require('../../assets/images/capture_it.jpeg'),
      projectName: 'Capture IT',
      projectDesc:
        'Developed an android application using Flutter that can be used to capture any Image around and translate text or perform OCR',
      descBullets: [
        ' Developed a mobile application using Flutter that allows users to search and understand the world around them using just their camera.',
        ' Integrated text translation, QR scanning, and optical character recognition (OCR) features using the firebase ML kit.',
        ' Developed a pipeline that enables the users to translate the text within images to over 50+ different languages.',
        ' Designed and implemented a user-friendly interface using Flutters built-in widgets and design elements.',
      ],
      technologiesUsed: [
        {
          skillName: 'HTML5',
          imageLink: require('../../assets/images/html5.png'),
        },
        {
          skillName: 'Dart',
          imageLink: dart,
        },
        {
          skillName: 'FireBase ML Kit',
          imageLink: require('../../assets/images/firebaseMLkit.png'),
        },
      ],
      footerLink: [
        {
          name: 'GitHub Link',
          url: 'https://github.com/darshilmehta/CaptureIT',
        },
      ],
    },
    {
      image: require('../../assets/images/sentiment_analysis.jpg'),
      projectName: 'Human Sentiment Analysis',
      projectDesc:
        'Developed a model that predict the sentiment of any readable human statement',
      descBullets: [
        ' Conducted research to determine the sentiment of customers for the products they utilize.',
        " Performed quantitative analysis on twitter's Sentiment140 dataset comprising of 1.6M tweets.",
        ' Pre-processed the dataset by removal of links, stop words and trained a word2vec model for the creation of an individual feature vector for different words in the dataset.',
        ' Analysed different models with varying depths of LSTM layers and achieved an overall accuracy of 78.88%.',
      ],
      technologiesUsed: [
        {
          skillName: 'Python',
          imageLink: require('../../assets/images/python.png'),
        },
        {
          skillName: 'TensorFlow',
          imageLink: require('../../assets/images/tensorflow.png'),
        },
        {
          skillName: 'Keras',
          imageLink: require('../../assets/images/keras.png'),
        },
        {
          skillName: 'Google Collab',
          imageLink: require('../../assets/images/collab.png'),
        },
      ],
      footerLink: [
        {
          name: 'Dataset Link',
          url: 'http://help.sentiment140.com/for-students',
        },
        {
          name: 'GitHub and Theory Link',
          url: 'https://github.com/darshilmehta/Sentiment-Analysis-NLP',
        },
      ],
    },
    {
      image: require('../../assets/images/plant_pathology.jpg'),
      projectName: 'Plant Pathology 2021 - FGVC8',
      projectDesc:
        'Developed a model that can identify and categorize plant leaf images into the 14 most common diseases',
      descBullets: [
        ' Conducted research to identify and categorize 14 different types of foliar disease found in apple tree leaves.',
        ' Utilized the dataset provided by Cornell Initiative for Digital Agriculture (CIDA) consisting of over 23,000 high resolution RGB images.',
        ' Enhanced the images by performing Contrast Limited Adaptive Histogram Equalization (CLAHE) and performed dataset-augmentation by rotating, shearing, flipping and shifting the images.',
        ' Achieved an F1-score of 0.91322 after fine tuning the Efficient-NetB7 model.',
      ],
      technologiesUsed: [
        {
          skillName: 'Python',
          imageLink: require('../../assets/images/python.png'),
        },
        {
          skillName: 'TensorFlow',
          imageLink: require('../../assets/images/tensorflow.png'),
        },
        {
          skillName: 'Keras',
          imageLink: require('../../assets/images/keras.png'),
        },
        {
          skillName: 'Google Collab',
          imageLink: require('../../assets/images/collab.png'),
        },
      ],
      footerLink: [
        {
          name: 'Dataset Link',
          url: 'https://www.kaggle.com/c/plant-pathology-2021-fgvc8/data',
        },
        {
          name: 'GitHub Link',
          url: 'https://github.com/darshilmehta/Plant-Pathology-2021',
        },
      ],
    },
    {
      image: require('../../assets/images/spotify.png'),
      projectName: 'Spotify Music Controller',
      projectDesc:
        'Developed an online web-portal where one can sync their spotify account and listen to songs with others in the room',
      descBullets: [
        ' Developed a web-application that enables various users to collaborate and listen to songs on spotify.',
        ' Constructed re-usable React-Redux components that help the users to create and room and invite other users.',
        ' Implemented features that assist the user to play, pause or skip songs based on the result of votes casted by all the members of the room.',
        ' Analysed and integrated various APIs provided by Spotify to gather live data regarding the song being played.',
      ],
      technologiesUsed: [
        {
          skillName: 'Python',
          imageLink: require('../../assets/images/python.png'),
        },
        {
          skillName: 'Django',
          imageLink: django,
        },
        {
          skillName: 'JavaScript',
          imageLink: require('../../assets/images/js.png'),
        },
        {
          skillName: 'ReactJS',
          imageLink: require('../../assets/images/react.png'),
        },
        {
          skillName: 'REST Api',
          imageLink: require('../../assets/images/rest.png'),
        },
      ],
      footerLink: [
        {
          name: 'GitHub Link',
          url: 'https://github.com/darshilmehta/spotify-music-controller',
        },
      ],
    },
    {
      image: require('../../assets/images/freshmart.jpg'),
      projectName: 'FreshMart',
      projectDesc:
        'Developed a web-application that helps grocery vendors get business online',
      descBullets: [
        ' Designed user-friendly screens for an online grocery store, enabling a better user experience for customer engagement.',
        ' Implemented a cart system that helps the customers review all the items and make specific changes if required.',
        ' Created an admin panel allowing the client monitor and manage the inventory in a practical way.',
        ' Implemented client-facing features that directly connects the customer and client in case of any mis-happening.',
      ],
      technologiesUsed: [
        {
          skillName: 'HTML 5',
          imageLink: require('../../assets/images/html5.png'),
        },
        {
          skillName: 'CSS 3',
          imageLink: require('../../assets/images/css3.png'),
        },
        {
          skillName: 'JavaScript',
          imageLink: require('../../assets/images/js.png'),
        },
        {
          skillName: 'Python',
          imageLink: require('../../assets/images/python.png'),
        },
        {
          skillName: 'Django',
          imageLink: django,
        },
      ],
      footerLink: [
        {
          name: 'GitHub Link',
          url: 'https://github.com/darshilmehta/FreshMart',
        },
      ],
    },
  ];

  function openUrlInNewTab(url) {
    if (!url) {
      return;
    }
    var win = window.open(url, '_blank');
    win.focus();
  }

  const { isDark } = useContext(StyleContext);

  return (
    <Fade bottom duration={1000} distance="20px">
      <div className="main" id="projects">
        <div>
          <h1 className="skills-heading">{'Projects '}</h1>
          <p
            className={
              isDark
                ? 'dark-mode project-subtitle'
                : 'subTitle project-subtitle'
            }
          >
            {'PROJECTS MADE WHILE LEARNING TECHNOLGIES'}
          </p>

          <div className="projects-container">
            {projects.map((project, i) => {
              return (
                <div
                  key={i}
                  className={
                    isDark
                      ? 'dark-mode project-card project-card-dark'
                      : 'project-card project-card-light'
                  }
                >
                  {project.image ? (
                    <div className="project-image">
                      <img
                        src={project.image}
                        alt={project.projectName}
                        className="card-image"
                      ></img>
                    </div>
                  ) : null}
                  <div className="project-detail">
                    <h5
                      className={isDark ? 'dark-mode card-title' : 'card-title'}
                    >
                      {project.projectName}
                    </h5>
                    <p
                      className={
                        isDark ? 'dark-mode card-subtitle' : 'card-subtitle'
                      }
                    >
                      {project.projectDesc}
                    </p>
                    {project.descBullets.map((item) => (
                      <p className="feature_item">{item}</p>
                    ))}
                    <SoftwareSkill list={project.technologiesUsed} />
                    {project.footerLink ? (
                      <div className="project-card-footer">
                        {project.footerLink.map((link, i) => {
                          return (
                            <span
                              key={i}
                              className={
                                isDark ? 'dark-mode project-tag' : 'project-tag'
                              }
                              onClick={() => openUrlInNewTab(link.url)}
                            >
                              {link.name}
                            </span>
                          );
                        })}
                      </div>
                    ) : null}
                  </div>
                </div>
              );
            })}
          </div>
          <div className="moreProjectsButton">
            <Button
              text={'More Projects'}
              href={'https://github.com/darshilmehta'}
              newTab={true}
            />
          </div>
        </div>
      </div>
    </Fade>
  );
}
