import React, { useContext } from 'react';
import './Achievement.scss';
import AchievementCard from '../../components/achievementCard/AchievementCard';
import { Fade } from 'react-reveal';
import StyleContext from '../../contexts/StyleContext';

export default function Achievement() {
  const { isDark } = useContext(StyleContext);

  const achievementSection = {
    title: 'Achievements and Certification ',
    subtitle:
      'Courses, Certificates and Achievements acquired along with acedamics',
    achievementsCards: [
      {
        title: 'Machine Learning for everyone',
        courseBy: 'Coursera',
        subtitle:
          'Successfully completed the Machine Learning course and learnt various techniques regarding Supervised and Non-supervised learning techniques and ML algorithms including Gradient Descent along with other regression and classification techniques. Also gained more intuition about Artificial Neural Networks and their applications.',
        image: require('../../assets/images/stanford.png'),
        footerLink: [
          {
            name: 'Course Link',
            url: 'https://www.coursera.org/learn/machine-learning',
          },
        ],
      },
      {
        title: 'DeepLearning Specialization',
        courseBy: 'DEEPLEARNING.AI',
        subtitle:
          'Successfully completed all the 5 courses including Object Dection, Localisation, Convolutional Neural Networks and Recurrent Neural Network. With the help of this course I gained deeper insights about image processing that helped me work of the Plant Pathology project.',
        image: require('../../assets/images/deeplearningspecialization.png'),
        footerLink: [
          {
            name: 'Course Link',
            url: 'https://www.coursera.org/specializations/deep-learning',
          },
        ],
      },
      {
        title: '30 Days of Google Cloud',
        courseBy: 'Google',
        subtitle:
          'Successfully completed 2 tracks with 6 courses each within 30 days. The two tracks included the Cloud Engineering Track and the Data Science Track. This course helped me upskill my knowledge regarding cloud services, gain practical knowledge regarding virtual training of models and Big Data Analysis.',
        image: require('../../assets/images/google_cloud.png'),
        footerLink: [
          {
            name: 'Certificate Link',
            url: 'https://drive.google.com/file/d/1u9gbooM-Op5yIub6_gVqJXe0_RadWApV/view',
          },
        ],
      },
    ],
  };

  return (
    <Fade bottom duration={1000} distance="20px">
      <div className="main" id="achievements">
        <div className="achievement-main-div">
          <div className="achievement-header">
            <h1
              className={
                isDark
                  ? 'dark-mode heading achievement-heading'
                  : 'heading achievement-heading'
              }
            >
              {achievementSection.title}
            </h1>
            <p
              className={
                isDark
                  ? 'dark-mode subTitle achievement-subtitle'
                  : 'subTitle achievement-subtitle'
              }
            >
              {achievementSection.subtitle}
            </p>
          </div>
          <div className="achievement-cards-div">
            {achievementSection.achievementsCards.map((card, i) => {
              return (
                <AchievementCard
                  key={i}
                  isDark={isDark}
                  cardInfo={{
                    title: card.title,
                    description: card.subtitle,
                    image: card.image,
                    footer: card.footerLink,
                    courseBy: card.courseBy,
                  }}
                />
              );
            })}
          </div>
        </div>
      </div>
    </Fade>
  );
}
