import React, { useContext } from 'react';
import './Skills.scss';
import SoftwareSkill from '../../components/softwareSkills/SoftwareSkill';
import { Fade } from 'react-reveal';
import StyleContext from '../../contexts/StyleContext';
import django from '../../assets/images/django.svg';
import wandb from '../../assets/images/wandb.svg';
import programmer from '../../assets/images/programmer.svg';

export default function Skills() {
  const { isDark } = useContext(StyleContext);

  const softwareSkillsDataScience = [
    {
      skillName: 'TensorFlow',
      imageLink: require('../../assets/images/tensorflow.png'),
    },
    {
      skillName: 'Keras',
      imageLink: require('../../assets/images/keras.png'),
    },
    {
      skillName: 'Python',
      imageLink: require('../../assets/images/python.png'),
    },
    {
      skillName: 'Hugging Faces',
      imageLink: require('../../assets/images/huggingfaces.png'),
    },
    {
      skillName: 'Weights and Biases',
      imageLink: wandb,
    },
    {
      skillName: 'Open CV',
      imageLink: require('../../assets/images/opencv.png'),
    },
  ];

  const skillsDataScience = [
    ' Keeping up with the new advancements in terms of different architectures being created',
    ' Implementing and working with the latest trends in the field of Artificial Intelligence, Deep Learning and Machine Learning',
    ' Gained experience while working as a research intern under Dr. Ninad Mehendale and while working on other research papers',
  ];

  const softwareSkillsWebDev = [
    {
      skillName: 'JavaScript',
      imageLink: require('../../assets/images/js.png'),
    },
    {
      skillName: 'TypeScript',
      imageLink: require('../../assets/images/ts.jpg'),
    },
    {
      skillName: 'Java',
      imageLink: require('../../assets/images/java.png'),
    },
    {
      skillName: 'Angular.JS',
      imageLink: require('../../assets/images/angular.png'),
    },
    {
      skillName: 'React.JS',
      imageLink: require('../../assets/images/react.png'),
    },
    {
      skillName: 'Redux.JS',
      imageLink: require('../../assets/images/redux.png'),
    },
    {
      skillName: 'Zustand',
      imageLink: require('../../assets/images/zustand.png'),
    },
    {
      skillName: 'Splunk',
      imageLink: require('../../assets/images/splunk.png'),
    },
    {
      skillName: 'Openshift',
      imageLink: require('../../assets/images/openshift.png'),
    },
    {
      skillName: 'Django',
      imageLink: django,
    },
    {
      skillName: 'Spring Boot',
      imageLink: require('../../assets/images/spring-boot.png'),
    },
    {
      skillName: 'Node JS',
      imageLink: require('../../assets/images/nodejs.png'),
    },
    {
      skillName: 'Supabase',
      imageLink: require('../../assets/images/supabase.jpg'),
    },
    {
      skillName: 'REST Api',
      imageLink: require('../../assets/images/rest.png'),
    },
    {
      skillName: 'HTML 5',
      imageLink: require('../../assets/images/html5.png'),
    },
    {
      skillName: 'CSS 3',
      imageLink: require('../../assets/images/css3.png'),
    },
    {
      skillName: 'SCSS',
      imageLink: require('../../assets/images/scss.png'),
    },
    {
      skillName: 'Bootstrap',
      imageLink: require('../../assets/images/bootstrap.png'),
    },
  ];

  const skillsWebDev = [
    ' Applying my skills to create efficient and user-friendly products to solve real-world problems',
    ' Experienced in working with ReactJs, Django, AngularJs, Spring Boot and Node',
    ' Gained practical knowledge my working on industry-level projects at TIAA, ComEd Learning, NTIPL and Pysber technologies',
  ];

  return (
    <div className={isDark ? 'dark-mode main' : 'main'} id="skills">
      <div>
        <h1 className={isDark ? 'dark-mode greeting-text' : 'greeting-text'}>
          {'Areas of Expertize'}
        </h1>
      </div>
      <div className="skills-main-div">
        <Fade left duration={1000}>
          <div className="skills-image-div">
            <img
              className="dsLogo"
              alt="Man Working"
              src={require('../../assets/images/datascience.gif')}
            />
          </div>
        </Fade>
        <Fade right duration={1000}>
          <div className="skills-text-div">
            <h1
              className={isDark ? 'dark-mode skills-heading' : 'skills-heading'}
              style={{ fontSize: '44px' }}
            >
              {'Data Science'}
            </h1>
            <p
              className={
                isDark
                  ? 'dark-mode subTitle skills-text-subtitle'
                  : 'subTitle skills-text-subtitle'
              }
            >
              {
                'UPSKILLING KNOWLEDGE OF DATA PRE-PROCESSING AND EXPLORING NEW ARCHITECTURES!'
              }
            </p>
            <SoftwareSkill list={softwareSkillsDataScience} />
            <div>
              {skillsDataScience.map((skills, i) => {
                return (
                  <p
                    key={i}
                    className={
                      isDark
                        ? 'dark-mode subTitle skills-text'
                        : 'subTitle skills-text'
                    }
                  >
                    {skills}
                  </p>
                );
              })}
            </div>
          </div>
        </Fade>
      </div>
      <div className="skills-main-div">
        <Fade left duration={1000}>
          <div className="skills-text-div">
            <h1
              className={isDark ? 'dark-mode skills-heading' : 'skills-heading'}
              style={{ fontSize: '44px' }}
            >
              {'Software Development'}
            </h1>
            <p
              className={
                isDark
                  ? 'dark-mode subTitle skills-text-subtitle'
                  : 'subTitle skills-text-subtitle'
              }
            >
              {'MAKING CONCISE WEB APPLICATIONS TO SOLVE REAL WORLD PROBLEMS'}
            </p>
            <SoftwareSkill list={softwareSkillsWebDev} />
            <div>
              {skillsWebDev.map((skills, i) => {
                return (
                  <p
                    key={i}
                    className={
                      isDark
                        ? 'dark-mode subTitle skills-text'
                        : 'subTitle skills-text'
                    }
                  >
                    {skills}
                  </p>
                );
              })}
            </div>
          </div>
        </Fade>
        <Fade right duration={1000}>
          <div className="skills-image-div">
            <img className="wdLogo" alt="Man Working" src={programmer} />
          </div>
        </Fade>
      </div>
    </div>
  );
}
