import React from "react";
import { Fade } from "react-reveal";
import "./ResearchCard.scss";

export default function researchCard({cardInfo, isDark}) {
  const GetLinkComponent = ({publicationType, publishedAt, isbnOrPaperId, linkOfPublication}) => {
    return (publicationType === "preprint") ? 
    (<div className="publication">
      <a style={{fontSize: 15, margin: "none", padding: "none"}} href={linkOfPublication} target="_blank" rel="noreferrer">{publishedAt}</a>
      <p style={{fontSize: 15, margin: "none", padding: "none"}}>Paper ID: {isbnOrPaperId}</p>
    </div>) : 
    (<div className="publication">
      <p style={{fontSize: 15, margin: "none", padding: "none"}}>{publishedAt}</p>
      <p style={{fontSize: 15, margin: "none", padding: "none"}}>ISBN : {isbnOrPaperId}</p>
    </div>);
  };

  return (
    <div>
      <Fade>
        <div className={isDark ? "research-card research-whiteborder" : "research-card research-blackborder"}>
          <div className="research-card-right">
            <div className="research-title-name-duration">
              {cardInfo.title}
            </div>
            <div>
              <GetLinkComponent 
                publicationType = {cardInfo.publicationType} 
                publishedAt = {cardInfo.publishedAt} 
                isbnOrPaperId = {cardInfo.isbnOrPaperId}
                linkOfPublication = {cardInfo.linkOfPublication}
              />
            </div>
            <div className="authorNameList" style={{margin: "none", padding: "none"}}>
              Authors : <i>{cardInfo.authors}</i>
            </div>
          </div>
        </div>
      </Fade>
    </div>
  );
}
